html, body {
    font-family: 'Nunito', sans-serif;
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
  }
  
  h1, p {
    font-family: Lato;
  }
  
  #root {
    width: 100%;
    height: 100%;
    background: #eee;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .app-container {
    width: 300px;
    background: #fff;
    padding: 50px;
    border-radius: 20px;
    box-shadow: 0 1px 55px 5px rgba(0,0,0,0.1);
    border: 5px solid #000;
  }
  
  .you-have-saved {
    font-size: 1.2em;
    text-align: center;
  }
  
  .amount-wrapper {
    display: flex;
    justify-content: center;
  }
  
  .amount-container {
    position: relative;
  }
  
  .money-icon {
    position: absolute;
    top: 0;
    left: 0;
    display: inline-block;
    font-size: 2em;
    width: 100px;
    height: 100px;
    text-align: center;
    background: #000;
    border-radius: 100px;
    line-height: 100px;
    z-index: 10;
    font-weight: 300;
    color: #eee;
  }
  
  .amount {
    display: inline-block;
    font-size: 2em;
    height: 94px;
    line-height: 100px;
    padding-left: 120px;
    padding-right: 40px;
    margin-left: 0px;
    background: #f2f2f2;
    border-radius: 100px;
    font-weight: 800;
    border: 3px solid #000;
  }
  
  .separator {
    height: 5px;
    width: 100%;
    background: #f9f9f9;
    border-radius: 10px;
    margin: 40px 0;
  }
  
  .buttons-container {
    display: flex;
    justify-content: space-between;
  }
  
  .button {
    width: 142px;
    height: 50px;
    border-radius: 50px;
    line-height: 44px;
    outline: none;
    background: #eee;
    font-size: 1.2em;
    cursor: pointer;
    border: 3px solid #000;
  }
  
  .button:hover {
    background: #ccc;
  }